import React from "react";
import { Wifi, Gym, Parking, Bar, Pool } from "@cvccorp-components/icons";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import List from "../List";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RoomListStoryProps = {
    mode: string,
    brand: BrandType,
    title: string,
    pax: number,
    days: number
}

const RoomListStory = ({
    mode, 
    brand,
    title,
    pax,
    days,
}: RoomListStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Room/List">
                <Typography variant="h3">RoomList</Typography>
                <br />
                <Divider />
                <br />
                <Grid>
                    <List 
                        product="hotel"
                        onClick={() => {}}
                        onDetail={() => {}}
                        thumb="http://lorempixel.com/output/technics-q-c-48-48-2.jpg"
                        title={title}
                        pax={pax}
                        days={days}
                        amenities={[
                            <Wifi size="small" key="1"/>,
                            <Gym size="small" key="2"/>,
                            <Parking size="small" key="3"/>,
                            <Pool size="small" key="4"/>,
                            <Bar size="small" key="5"/>,
                            <Wifi size="small" key="6"/>,
                            <Gym size="small" key="7"/>,
                            <Parking size="small" key="8"/>,
                            <Pool size="small" key="9"/>,
                            <Bar size="small" key="10"/>
                        ]}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        title: "Quarto Standard",
        pax: 2,
        days: 3
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        title: { type: "string" },
        pax: { type: "number" },
        days: { type: "number" }
    }
}

storiesOf("Components/Room/List", module)
    .add("Default", (args) => <RoomListStory {...args}/>, argsControls);
